body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: honeydew;
}

.game{
  justify-content: center;
  margin-top: 60px;
}
.heading{
  text-align: center;
}
ol,
ul {
  padding-left: 30px;
}
.status{
  font-size: large;
  margin: 0 0 0 15px;
}
.game-info button{
  margin: 5px;
  padding: 5px;
  display: block;
  background-color: bisque;
  border: 1px solid grey;
  border-radius: 5px;
}
.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
  
}
